@import url(https://fonts.googleapis.com/css2?family=Stint+Ultra+Condensed&display=swap);
body {
  font-family: 'Stint Ultra Condensed', sans-serif;
  margin: 0;
  font-size: 1.5em;
  text-align: center;
  background: black;
  color: white;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.card {
    position: relative;
    cursor: pointer;
}
.card img {
    width: 100%;
    display: block;
    border: 3px solid gold;
    border-radius: 6px;
}

/* front of card */
.card .front {
    -webkit-transform: rotateY(90deg);
            transform: rotateY(90deg);
    transition: all ease-in .2s;
    position: absolute;
}
.flipped .front {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
    transition-delay: .2s;
}

/* back of card */
.card .back {
    transition: all ease-in .2s;
    transition-delay: .2s;
}
.flipped .back {
    -webkit-transform: rotateY(90deg);
            transform: rotateY(90deg);
    transition-delay: 0s;
}

/* matching cards disappear */
.disappear {
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s 2s, opacity 5s linear;
}

@media screen and (max-width: 900px) {
    .card img {
        width: 90%;
    }
  }

@media screen and (max-width: 800px) {
    .card img {
        width: 80%;
    }
}

@media screen and (max-width: 770px) {
    .card img {
        width: 100%;
    }
}



.App {
  width: 100vh;
  max-width: 100vw;
  margin: 2.5em auto;
}

button {
  background: none;
  border: 2px solid white;
  padding: 6px 12px;
  border-radius: 4px;
  color: white;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
}

button:hover {
  background: gray;
  color: white;
}

.card-grid {
  margin-top: .5em;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-gap: 1.25em;
}

/* 
  
*/

@media screen and (max-width: 900px) {
  .card-grid {
    grid-template-columns: repeat(6, 1fr);
    grid-gap: .15em;
    margin: 2.5em .5em;
  }
}

@media screen and (max-width: 800px) {
  .card-grid {
    grid-template-columns: repeat(4, 1fr);
    grid-gap: .125em;
  }
}

@media screen and (max-width: 770px) {
  .card-grid {
    grid-template-columns: repeat(2, 1fr);
    grid-gap: .5em;
    margin: 2.5em 1em;
  }
}
