.card {
    position: relative;
    cursor: pointer;
}
.card img {
    width: 100%;
    display: block;
    border: 3px solid gold;
    border-radius: 6px;
}

/* front of card */
.card .front {
    transform: rotateY(90deg);
    transition: all ease-in .2s;
    position: absolute;
}
.flipped .front {
    transform: rotateY(0deg);
    transition-delay: .2s;
}

/* back of card */
.card .back {
    transition: all ease-in .2s;
    transition-delay: .2s;
}
.flipped .back {
    transform: rotateY(90deg);
    transition-delay: 0s;
}

/* matching cards disappear */
.disappear {
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s 2s, opacity 5s linear;
}

@media screen and (max-width: 900px) {
    .card img {
        width: 90%;
    }
  }

@media screen and (max-width: 800px) {
    .card img {
        width: 80%;
    }
}

@media screen and (max-width: 770px) {
    .card img {
        width: 100%;
    }
}


